import React from 'react';
import Helmet from 'react-helmet';
import { StaticQuery, graphql } from 'gatsby';

export const query = graphql`
    query SEOComponentQuery {
        site {
            siteMetadata {
                title
                description
            }
        }
    }
`;

const SEO = () => {
    // https://www.gatsbyjs.com/docs/static-query/
    return (
        <StaticQuery
            query={query}
            render={data => (
                <Helmet>
                    <html lang="hu" />
                    <meta charset="utf-8" />
                    <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />
                    <title>{data.site.siteMetadata.title}</title>

                    {/* <meta name="identifier-url" content="https://mandlee.github.io/" /> */}
                    <meta name="title" content={data.site.siteMetadata.title} />
                    <meta name="description" content={data.site.siteMetadata.description} />
                    {/* <meta name="keywords" content="javascript, es6, css, scss, sitebuild, react.js, d3js" /> */}
                    {/* <meta name="author" content="Bence Mndli" /> */}
                </Helmet>
            )}
        />
    );
};

export default SEO;
