import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser, faQuoteRight } from '@fortawesome/free-solid-svg-icons';

export const testimonialQuery = graphql`
    query TestimonialQuery {
        site {
            siteMetadata {
                testimonials {
                    name
                    text
                }
            }
        }
    }
`;

const Testimonials = () => {
    return (
        <StaticQuery
            query={testimonialQuery}
            render={({
                site: {
                    siteMetadata: { testimonials },
                },
            }) => (
                <section id="testimonial">
                    <div className="container">
                        <h2>Check out our customer reviews</h2>
                        <div className="testimonials">
                            {testimonials.map(({ name, text }) => (
                                <div className="testimonial" key={name}>
                                    <FontAwesomeIcon icon={faQuoteRight} size="3x" className="testimonial__quote" />
                                    <div>
                                        <FontAwesomeIcon icon={faUser} size="2x" className="testimonial__thumbnail" />
                                    </div>
                                    <div>
                                        <h3>{name}</h3>
                                        <p>{text}</p>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </section>
            )}
        />
    );
};

export default Testimonials;
