import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faCoffee, faGavel, faBalanceScaleRight } from '@fortawesome/free-solid-svg-icons';

library.add(faGavel, faCoffee, faBalanceScaleRight);

export const servicesQuery = graphql`
    query ServicesQuery {
        site {
            siteMetadata {
                services {
                    title
                    icon
                    description
                }
            }
        }
    }
`;

const Services = () => {
    return (
        <StaticQuery
            query={servicesQuery}
            render={({
                site: {
                    siteMetadata: { services },
                },
            }) => (
                <section id="services">
                    <div className="container">
                        <h2>What can we help you with</h2>
                        <div className="service-container">
                            {services.map(service => (
                                <div className="service-item" key={service.title}>
                                    {service.icon && (
                                        <FontAwesomeIcon className="service-icon" icon={service.icon} size="3x" />
                                    )}
                                    <h3 className="service-title">{service.title}</h3>
                                    <p className="description">{service.description}</p>
                                </div>
                            ))}
                        </div>
                    </div>
                </section>
            )}
        />
    );
};

export default Services;
