import React, { useState } from 'react';
import { StaticQuery, graphql } from 'gatsby';
import logo from './../../static/lawyer.svg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons';
// const SmoothScroll = typeof window !== `undefined` ? require('smooth-scroll') : null;

export const menuItemsQuery = graphql`
    query MenuItemsQuery {
        site {
            siteMetadata {
                profile {
                    name
                    title
                }
                navigation {
                    menu {
                        title
                        url
                    }
                    cta {
                        title
                        url
                    }
                }
            }
        }
    }
`;

const Header = () => {
    const [isOpen, setOpen] = useState(false);
    // useEffect(() => {
    //     new SmoothScroll('a[href*="#"]', {
    //         header: '.navbar',
    //         offset: 20,
    //     });
    // });

    let menuClass = 'menu navbar-nav';
    if (isOpen) {
        menuClass += ' active';
    }

    return (
        <StaticQuery
            query={menuItemsQuery}
            render={({
                site: {
                    siteMetadata: {
                        navigation: { menu, cta },
                        profile: { name, title },
                    },
                },
            }) => (
                <header>
                    <nav className="nav">
                        <div className="nav-bar container">
                            <div className="navbar-brand">
                                <img src={logo} className="nav-logo" alt="logo" />
                                <span>{name}</span>
                                <span className="sub-title">{title}</span>
                            </div>
                            <div className="toggle">
                                <a href="#0" onClick={() => setOpen(!isOpen)}>
                                    <FontAwesomeIcon icon={isOpen ? faTimes : faBars} size="2x" />
                                </a>
                            </div>
                            <div className="navbar-collapse">
                                <ul className={menuClass}>
                                    {menu.map(({ title, url }) => (
                                        <li className="item" key={title}>
                                            <a href={url}>{title}</a>
                                        </li>
                                    ))}
                                    <li className="item cta" key={cta.title}>
                                        <a className="btn btn-primary" href={cta.url} role="button">
                                            {cta.title}
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </nav>
                </header>
            )}
        />
    );
};

export default Header;
