import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import SocialIcons from './SocialIcons';
import ContactInfo from './ContactInfo';
import avatar from './../../static/lawyer_profile.jpg';

export const profileQuery = graphql`
    query HeroComponentQuery {
        site {
            siteMetadata {
                title
                profile {
                    name
                    title
                    description
                }
            }
        }
    }
`;

const Hero = () => {
    return (
        <StaticQuery
            query={profileQuery}
            render={({
                site: {
                    siteMetadata: { profile },
                },
            }) => (
                <section id="home" className="hero">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-12 col-md-4">
                                <img src={avatar} className="avatar" alt={profile.name} />
                            </div>
                            <div className="col-sm-12 col-md-8 hero-content">
                                <div className="row">
                                    <div className="col-sm-12">
                                        <h1>{profile.name}</h1>
                                        <h3>{profile.title}</h3>
                                        <SocialIcons size="2x" />
                                        <p className="description">{profile.description}</p>
                                    </div>
                                </div>
                                <div>
                                    <ContactInfo />
                                    <div className="row">
                                        <div className="col-12">
                                            <a className="btn btn-primary hero-cta" href="#contact" role="button">
                                                Contact me
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            )}
        />
    );
};
export default Hero;
