import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faFacebookSquare } from '@fortawesome/free-brands-svg-icons';

export const socialQuery = graphql`
    query SocialQuery {
        site {
            siteMetadata {
                profile {
                    social {
                        facebook
                        linkedin
                    }
                }
            }
        }
    }
`;

const SocialIcons = ({ size = 'lg' }) => (
    <StaticQuery
        query={socialQuery}
        render={({
            site: {
                siteMetadata: {
                    profile: {
                        social: { facebook, linkedin },
                    },
                },
            },
        }) => (
            <div className="social">
                <a href={linkedin} target="_blank" rel="noreferrer">
                    <FontAwesomeIcon icon={faLinkedin} size={size} />
                </a>{' '}
                <a href={facebook} target="_blank" rel="noreferrer">
                    <FontAwesomeIcon icon={faFacebookSquare} size={size} />
                </a>
            </div>
        )}
    />
);

export default SocialIcons;
