import React from 'react';
import { StaticQuery } from 'gatsby';
import { servicesQuery } from './Services';
import { menuItemsQuery } from './Header';
import SocialIcons from './SocialIcons';
import ContactInfo from './ContactInfo';

const Footer = () => (
    <footer className="footer">
        <section id="footer">
            <div className="container">
                <div className="row">
                    <div className="col-md-3 col-sm-12">
                        {/* TODO */}
                        <h4>Name</h4>
                        <p className="description">
                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Velit ducimus voluptatibus neque
                            illo id repellat quisquam? Autem expedita earum quae laborum ipsum ad.
                        </p>
                        <SocialIcons />
                    </div>
                    <div className="col-md-3 col-sm-12">
                        <h4>What We Do</h4>
                        <StaticQuery
                            query={servicesQuery}
                            render={({
                                site: {
                                    siteMetadata: { services },
                                },
                            }) => (
                                <ul>
                                    {services.map(service => (
                                        <li key={service.title}>{service.title}</li>
                                    ))}
                                </ul>
                            )}
                        />
                    </div>
                    <div className="col-md-3 col-sm-12">
                        <h4>Navigate</h4>
                        <StaticQuery
                            query={menuItemsQuery}
                            render={({
                                site: {
                                    siteMetadata: {
                                        navigation: { menu, cta },
                                    },
                                },
                            }) => (
                                <ul>
                                    {menu.map(({ title, url }) => (
                                        <li key={title}>
                                            <a href={url}>{title}</a>
                                        </li>
                                    ))}
                                    <li key={cta.title}>
                                        <a href={cta.url}>{cta.title}</a>
                                    </li>
                                </ul>
                            )}
                        />
                    </div>
                    <div className="col-md-3 col-sm-12">
                        <h4>Contact us</h4>
                        <ContactInfo oneLiner={true} />
                    </div>
                </div>
                <div className="copyright row">
                    <div className="col-sm-12"> TODO {new Date().getFullYear()}</div>
                </div>
            </div>
        </section>
    </footer>
);

export default Footer;
