import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { GoogleMap, Marker, LoadScript } from '@react-google-maps/api';
import office from './../../static/office.jpg';
import ContactInfo from './ContactInfo';

const containerStyle = {
    width: '100%',
    height: '325px',
    borderRadius: '10px',
};

const center = {
    lat: -3.745,
    lng: -38.523,
};
const googleMapsApiKey = 'AIzaSyAGNdPrA1akEXBJWGe51h6egUmFbh0EJhM';

export const officeQuery = graphql`
    query OfficeQuery {
        site {
            siteMetadata {
                profile {
                    office {
                        address
                        openingHours {
                            day
                            hours
                        }
                    }
                    contact {
                        phone
                        email
                    }
                }
            }
        }
    }
`;

const Office = () => {
    return (
        <StaticQuery
            query={officeQuery}
            render={({
                site: {
                    siteMetadata: {
                        profile: {
                            office: { address, openingHours },
                            contact: { phone, email },
                        },
                    },
                },
            }) => (
                <section id="office">
                    <div className="container">
                        <h2>Office</h2>
                        <LoadScript googleMapsApiKey={googleMapsApiKey}>
                            <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={10}>
                                <Marker position={center} />
                            </GoogleMap>
                        </LoadScript>
                        <div className="office-details row" id="contact">
                            <div className="col-xs-12 col-sm-6 col-md-4">
                                {/* TODO */}
                                <img src={office} alt="" />
                            </div>
                            <div className="col-xs-12 col-sm-6 col-md-4">
                                <h4>Opening Hours</h4>
                                <div>
                                    {openingHours.map(({ day, hours }) => (
                                        <div key={day} className="opening-hours__row">
                                            <strong className="opening-hours__day">{day}:</strong>{' '}
                                            <span className="opening-hours__time">{hours}</span>
                                        </div>
                                    ))}
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-12 col-md-4">
                                <h4>Address</h4>
                                <ContactInfo oneLiner={true} />
                            </div>
                        </div>
                    </div>
                </section>
            )}
        />
    );
};

export default Office;
